import React from 'react';
import './Game.css';

function ColorOptions({ options, onSelect }) {
  return (
    <div className="options-container">
      {options.map((color, index) => (
        <button key={index} className="button" style={{ backgroundColor: color.toLowerCase() }} onClick={() => onSelect(color)}>
          {color}
        </button>
      ))}
    </div>
  );
}


export default ColorOptions;
