import React, { useState, useEffect } from "react";
import ColorOptions from "./ColorOptions";
import Timer from "./Timer";
import "./Game.css";

function Game() {
  const colors = ["Red", "Blue", "Green", "Yellow", "Purple", "Orange"];
  const [currentColor, setCurrentColor] = useState("");
  const [currentText, setCurrentText] = useState("");
  const [options, setOptions] = useState([]);
  const [score, setScore] = useState(0);
  const [timeLeft, setTimeLeft] = useState(30);
  const [gameStarted, setGameStarted] = useState(false);

  useEffect(() => {
    if (gameStarted) {
      generateColor();
      const timer = setInterval(() => {
        setTimeLeft((prevTime) => {
          if (prevTime <= 1) clearInterval(timer);
          return prevTime - 1;
        });
      }, 1000);
      return () => clearInterval(timer);
    }
  }, [gameStarted]);

  const startGame = () => {
    setGameStarted(true);
    setTimeLeft(30);
    setScore(0);
    generateColor();
  };

  const restartGame = () => {
    setGameStarted(false);
    startGame();
  };

  const generateColor = () => {
    const colorIndex = Math.floor(Math.random() * colors.length);
    const textIndex =
      (colorIndex + Math.floor(Math.random() * (colors.length - 1)) + 1) %
      colors.length;
    setCurrentColor(colors[colorIndex]);
    setCurrentText(colors[textIndex]);
    setOptions(shuffleColors(colors));
  };

  const shuffleColors = (colors) => {
    let shuffled = [...colors];
    for (let i = shuffled.length - 1; i > 0; i--) {
      const j = Math.floor(Math.random() * (i + 1));
      [shuffled[i], shuffled[j]] = [shuffled[j], shuffled[i]];
    }
    return shuffled;
  };

  const checkAnswer = (selectedColor) => {
    if (selectedColor === currentColor) {
      // Player should match based on the actual color
      setScore(score + 1);
      generateColor(); // Generate new colors immediately after a correct guess
    }
  };

  const CustomSVG = () => (
    <svg
      xmlns="http://www.w3.org/2000/svg"
      id="test111"
      data-name="\u56FE\u5C42 1"
      viewBox="0 0 116.84 21.04"
    >
      <defs>
        <style
          dangerouslySetInnerHTML={{
            __html: `
            .cls-1 { fill: #e7352c; }
            .cls-2 { fill: #333; }
          `,
          }}
        />
      </defs>
      <path
        d="M6.93 15.65a1.48 1.48 0 1 1-1.48-1.48 1.48 1.48 0 0 1 1.48 1.48M19.84 14.11a15.34 15.34 0 0 0-13-13 9.73 9.73 0 0 0-2.15 1.55v1.43a12.18 12.18 0 0 1 12.17 12.16h1.43a10.25 10.25 0 0 0 1.55-2.14"
        class="cls-1"
      ></path>
      <path
        d="M21 9.62A9.62 9.62 0 0 0 11.33 0c-.34 0-.67 0-1 .05l-.22.64a16.55 16.55 0 0 1 10.14 10.13l.64-.23c0-.32.06-.64.06-1M11.44 21A11.44 11.44 0 0 1 0 9.61a11.36 11.36 0 0 1 3.35-8.09l.65.61a10.56 10.56 0 0 0 0 15 10.56 10.56 0 0 0 15 0l.61.61A11.35 11.35 0 0 1 11.44 21"
        class="cls-1"
      ></path>
      <path
        d="M11.31 16.91A6.65 6.65 0 0 0 8.7 11a6.51 6.51 0 0 0-3.35-1.36.61.61 0 0 1-.53-.64.59.59 0 0 1 .65-.54 7.85 7.85 0 0 1 7 8.58 6.85 6.85 0 0 1-.26 1.35l1.74.49a9.66 9.66 0 0 0 1.5-.57 10.64 10.64 0 0 0 .19-2.05A11 11 0 0 0 6.31 5.4a4.84 4.84 0 0 0-1.75 0 3.81 3.81 0 0 0-1.89 1.17 3.74 3.74 0 0 0 1.67 6 8.66 8.66 0 0 0 .94.17A3.51 3.51 0 0 1 8.2 16.2a3.47 3.47 0 0 1-.56 1.89l1.2.77a8.91 8.91 0 0 0 1.8.3 6.58 6.58 0 0 0 .66-2.3"
        class="cls-1"
      ></path>
      <path
        d="M104.18 3.17h2.21v14.7h-2.21zM68.39 11.73a2.51 2.51 0 0 0 1.46-2.41V5.81a2.64 2.64 0 0 0-.78-1.95 2.6 2.6 0 0 0-1.78-.7h-6.38v14.73h2.22V12h2.27a2 2 0 0 1 2 1.74l.87 4.17h2.42L69.75 14a3 3 0 0 0-1.36-2.22m-5.26-6.54h3.8a.75.75 0 0 1 .71.84v3a.75.75 0 0 1-.71.84h-3.8ZM29.09 3.87a2.67 2.67 0 0 0-.78 2v9.4a2.64 2.64 0 0 0 .78 1.95 2.62 2.62 0 0 0 1.78.7h6v-2.1h-5.6a.76.76 0 0 1-.72-.84V11.2H36V9.14h-5.48V6.08a.76.76 0 0 1 .72-.84h5.6V3.17h-6a2.66 2.66 0 0 0-1.78.7M111.24 5.24h5.6V3.17h-6a2.66 2.66 0 0 0-1.78.7 2.71 2.71 0 0 0-.78 2v12h2.21V11.2H116V9.14h-5.52V6.08a.76.76 0 0 1 .72-.84M45.2 9.14h-3.43a.79.79 0 0 1-.77-.79V6.08a.75.75 0 0 1 .71-.84h5.49V3.17h-5.87a2.66 2.66 0 0 0-1.78.7 2.67 2.67 0 0 0-.78 2v3.01a2.59 2.59 0 0 0 2.58 2.59h3.44a.79.79 0 0 1 .79.79V15a.76.76 0 0 1-.72.84h-6.09v2.07h6.46a2.55 2.55 0 0 0 2.56-2.66v-3.5a2.58 2.58 0 0 0-2.59-2.61M72.65 3.87a2.67 2.67 0 0 0-.78 2v9.4a2.64 2.64 0 0 0 .78 1.95 2.62 2.62 0 0 0 1.78.7h6v-2.1h-5.6a.75.75 0 0 1-.71-.84V11.2h5.51V9.14h-5.55V6.08a.75.75 0 0 1 .71-.84h5.6V3.17h-6a2.66 2.66 0 0 0-1.78.7M88.76 9.14h-3.43a.79.79 0 0 1-.79-.79V6.08a.75.75 0 0 1 .71-.84h5.51V3.17h-5.88a2.64 2.64 0 0 0-1.77.7 2.67 2.67 0 0 0-.78 2v3.01a2.58 2.58 0 0 0 2.58 2.59h3.43a.79.79 0 0 1 .79.79V15a.75.75 0 0 1-.71.84h-6.09v2.07h6.46a2.6 2.6 0 0 0 1.77-.71 2.63 2.63 0 0 0 .79-2v-3.5a2.58 2.58 0 0 0-2.59-2.58M99.6 9.14h-3.43a.79.79 0 0 1-.79-.79V6.08a.75.75 0 0 1 .71-.84h5.51V3.17h-5.87a2.66 2.66 0 0 0-1.78.7 2.67 2.67 0 0 0-.78 2v3.01a2.59 2.59 0 0 0 2.58 2.59h3.44a.79.79 0 0 1 .79.79V15a.76.76 0 0 1-.72.84h-6.09v2.07h6.46a2.55 2.55 0 0 0 2.56-2.66v-3.5a2.58 2.58 0 0 0-2.59-2.61M56.48 3.16h-6.69v14.73H52v-5.72h4.48a2.57 2.57 0 0 0 1.77-.7 2.63 2.63 0 0 0 .79-2V5.81a2.65 2.65 0 0 0-.79-1.95 2.57 2.57 0 0 0-1.77-.7m.34 6.11a.75.75 0 0 1-.71.84H52V5.22h4.11a.75.75 0 0 1 .71.84Z"
        class="cls-2"
      ></path>
    </svg>
  );

  return (
    <section class="recommendations-layout">
      <div class="_layout-navbar_1tc8r_5">
        <div class="_navbar_t7zix_1">
          <div class="_left_t7zix_9">
            <div class="_logo_t7zix_13">
              <CustomSVG></CustomSVG>
            </div>
          </div>
        </div>
      </div>
      <div className="game-container">
        <h1 className="game-header">Color Matching Game</h1>
        {!gameStarted ? (
          <button className="button start-button" onClick={startGame}>
            Start Game
          </button>
        ) : (
          <>
            <Timer timeLeft={timeLeft} />
            <div>
              <p className="question">
                What is the color of this word?{" "}
                <strong style={{ color: currentColor.toLowerCase() }}>
                  {currentText}
                </strong>
              </p>
              <ColorOptions options={options} onSelect={checkAnswer} />
            </div>
            <p className="score">Score: {score}</p>
            {timeLeft <= 0 ? (
              <div>
                <h2 className="game-over">Game Over</h2>
                <p className="score">Your final score is: {score}</p>
              </div>
            ) : (
              <button className="button restart-button" onClick={restartGame}>
                Restart
              </button>
            )}
          </>
        )}
      </div>
      <footer class="arco-layout-footer _footer_odg74_1">
        <div>Copyright © 2024 Espressif Systems. All rights reserved.</div>
      </footer>
    </section>
  );
}

export default Game;
